import React from "react";
import { Link } from "react-router-dom";
import useFetch from "../../hooks/UseFetch";
import "../homeMainContent/homeMainContent.scss";
const NewsSections = ({ query }) => {
  const { data } = useFetch(`news/dependoncat/?cat=${query}`);

  return (
    <>
      {data.length > 0 && (
        <>
          <Link to={`/news/${data[0]._id}`}>
            <div className="bestNew">
              <img src={data[0].img} alt="" />
              <div className="desc">{data[0].title}</div>
            </div>
          </Link>
          <div className="bottomNews">
            {data.map((n, i) => {
              if (i > 0 && i < 4) {
                return (
                  <Link to={`/news/${n._id}`}>
                    <div className="one">
                      <img src={n.img} alt="" />
                      <div className="desc">{n.title} </div>
                    </div>
                  </Link>
                );
              }
            })}
          </div>
        </>
      )}
    </>
  );
};

export default NewsSections;
