import "./news.scss";
import React, { useEffect, useRef, useState } from "react";
import {Link} from "react-router-dom"
import useFetch from "../../hooks/UseFetch";
const News = ({words}) => {
  // let words = [
  //   "شاهد كريستيانو يصب غضبه على عضو بجهاز الخليج - صور ",
  //   "هنري يوبخ ميسي وباريس سان جيرمان",
  //   "النصر يخدم الاتحاد بتعثر جديد أمام الخليج",
  // ];

  let [delta, setDelta] = useState(140);
  let [text, setText] = useState("");
  let [loopnum, setLoopnum] = useState(0);
  let [isDeleted, setIsDeleted] = useState(false);
  let time = 140;

  useEffect(() => {
    let player = setInterval(() => {
      tick();
    }, delta);
    return () => {
      clearInterval(player);
    };
  }, [text]);
  let tick = () => {
    let i = loopnum % words.length;
    let fullText = words[i];
    let showText = isDeleted
      ? fullText.substring(0, text.length - 1)
      : fullText.substring(0, text.length + 1);
    setText(showText);

    if (isDeleted) {
      setDelta((prevDelta) => prevDelta / 100000);
    }

    if (!isDeleted && showText === fullText) {
      setIsDeleted(true);
      setDelta(time);
    } else if (isDeleted && showText === "") {
      setIsDeleted(false);
      setLoopnum(loopnum + 1);
      setDelta(140);
    }
  };
  return (
    // <Link to="/news/4545" className="newslink">
    <div className="news">
      صحيفة التاسعة برس - آخر الاخبار /<span>{text}<span className="ityped-cursor"></span></span>
      
    </div>
    // </Link>
  );
};

export default News;