import Slider from "../../components/slider/SliderChat";
import Taps from "../../components/taps/Taps";
import "./home.scss";
import { sliderImages } from "../../data";
import HomeMainContent from "../../components/homeMainContent/HomeMainContent";
import { useLocation } from "react-router-dom";
import { useEffect } from "react";
import Ad from "../../components/ad/Ad";
import useFetch from "../../hooks/UseFetch.js";

const Home = () => {
  const location = useLocation();
  const path = location.pathname;
  console.log(path);
  useEffect(() => {
    if (path === "/") {
      const sidebar = document.querySelector(".sidebar");
      const middleContent = document.querySelector(".middleContent");
      sidebar.style.display = "none";
      middleContent.style.display = "block";
    }
    document.title = "صحيفة التاسعة برس";
  }, [path]);

  const { data, loading } = useFetch("news/slider");
  const { data: adsUnderNavbar } = useFetch(
    "ads/dependoncat/?position=under navbar"
  );

  return (
    <div>
      {data.length > 0 && <Slider images={data} />}

      <div className="ads">
        {adsUnderNavbar.length > 0 &&
          adsUnderNavbar.map((ad, i) => (
            <>
              {i < 7 && (
                <Ad
                  adLink={ad.img}
                  adSocial={ad.link}
                  id={ad._id}
                  width={"945px"}
                />
              )}
            </>
          ))}
      </div>

      <Taps />
      <HomeMainContent />
    </div>
  );
};

export default Home;
