import React, { useEffect } from "react";
import "./single.scss";
import { useLocation } from "react-router-dom";
import {
  InstapaperIcon,
  LinkedinIcon,
  PinterestIcon,
  RedditIcon,
  TelegramIcon,
  TwitterIcon,
  WhatsappIcon,
  FacebookIcon,
  FacebookShareButton,
  InstapaperShareButton,
  LinkedinShareButton,
  PinterestShareButton,
  RedditShareButton,
  TelegramShareButton,
  TwitterShareButton,
  WhatsappShareButton,
} from "react-share";
import Ad from "../../components/ad/Ad";
import useFetch from "../../hooks/UseFetch";
import moment from "moment";
import { makeRequest } from "../../axios";
// import 'moment/locale/ar';
const Single = () => {
  const location = useLocation();
  const path = location.pathname.split("/")[2];
  console.log(path);
  useEffect(() => {
    if (path !== "/") {
      const sidebar = document.querySelector(".sidebar");
      const middleContent = document.querySelector(".middleContent");
      sidebar.style.display = "block";
      middleContent.style.display = "flex";
    }

  }, [path]);
  useEffect(() => {
    const handleClick = async() => {
      try {
        await makeRequest.put(`news/view/${path}`)
      }catch(err) {
        console.log(err);
      }
    }
    return () => handleClick()
  },[])
  const { data, loading } = useFetch(`news/${path}`);
  console.log(data);
  const currentUrl = window.location.href;

    const renderText = (text) => {
    const lines = text.split("\n");
    return lines.map((line, index) => (
      <React.Fragment key={index}>
        {line}
        {index !== lines.length - 1 && <br />}
      </React.Fragment>
    ));
  };
  const { data: adsUnderNavbar } = useFetch("ads/dependoncat/?position=under navbar");
     
 
  return (
    <div className="singleNews">
       {/* <div className="ads">
      {adsUnderNavbar.length > 0 &&
        adsUnderNavbar.map((ad) => (
          <>
            <Ad adLink={ad.img} adSocial={ad.link}  width={"100%"}/>
          </>
        ))} */}

<div className="ads">
        {adsUnderNavbar.length > 0 &&
          adsUnderNavbar.map((ad, i) => (
            <>
              {i < 7 && (
                <Ad
                  adLink={ad.img}
                  adSocial={ad.link}
                  id={ad._id}
                  width={"100%"}
                />
              )}
            </>
          ))}
      {/* </div> */}
    </div>
      {data.title && (
        <>
          <div className="title">
            {data.title}
            <span>+</span>
          </div>
          <div className="category">{data.cat}</div>
          <div className="time">
            {moment(data.createdAt).locale("ar").format("LLLL")}
          </div>
          <img src={data.img} alt="" />
          <div className="desc"> {renderText(data.desc)}</div>
          {data.video.length > 0 && (
            <div className="yvideo">
              <iframe
                width="100%"
                height="365"
                src={data.video}
                frameborder="0"
                allow="autoplay; encrypted-media"
                allowfullscreen
              ></iframe>
            </div>
          )}
        </>
      )}
      <div className="shareContainer">
        <FacebookShareButton url={currentUrl}>
          <FacebookIcon size={35} />
        </FacebookShareButton>

        <TwitterShareButton url={currentUrl}>
          <TwitterIcon size={35} />
        </TwitterShareButton>

        <WhatsappShareButton url={currentUrl}>
          <WhatsappIcon size={35} />
        </WhatsappShareButton>

        <LinkedinShareButton url={currentUrl}>
          <LinkedinIcon size={35} />
        </LinkedinShareButton>

        <PinterestShareButton url={currentUrl}>
          <PinterestIcon size={35} />
        </PinterestShareButton>

        <TelegramShareButton url={currentUrl}>
          <TelegramIcon size={35} />
        </TelegramShareButton>

        <RedditShareButton url={currentUrl}>
          <RedditIcon size={35} />
        </RedditShareButton>

        <InstapaperShareButton url={currentUrl}>
          <InstapaperIcon size={35} />
        </InstapaperShareButton>
      </div>
      <Ad
        adLink={"https://www.al-wattan.net/Uploads/Ads/Enma-Banner.gif"}
        height={"138px"}
        width={"650px"}
      />
      <Ad
        adLink={"https://www.al-wattan.net/Uploads/Ads/Enma-Banner.gif"}
        height={"138px"}
        width={"650px"}
      />
    </div>
  );
};

export default Single;
