import { makeRequest } from "../../axios";
import "./ad.scss";

const Ad = ({ adLink, width, height, adSocial, id}) => {

  const handleClick = async() => {
    try {
      await makeRequest.put(`ads/click/${id}`)
    }catch(err) {
      console.log(err);
    }
  }
  return (
    <div className="ad" style={{ width: width, height}} onClick={handleClick}>
      <a href={adSocial} target="_blank">
        <img src={adLink} alt="" />
      </a>
    </div>
  );
};

export default Ad;
