import React, { useState, useEffect } from "react";
import "./slider.scss";
import {Link} from "react-router-dom"

function Slider(props) {
  const [selectedImage, setSelectedImage] = useState(props.images[0].img);
  const [selectedtitle, setSelectedtitle] = useState(props.images[0].title);
  const [selectedid, setSelectedid] = useState(props.images[0]._id);
  const [currentSlide, setCurrentSlide] = useState(0);

  const handleImageClick = (image, title) => {
    setSelectedImage(image);
    setSelectedtitle(title);
  };

  const handleCloseClick = () => {
    setSelectedImage(null);
  };

  const handleAutoPlay = () => {
    setCurrentSlide((currentSlide + 1) % props.images.length);
    console.log(currentSlide);
    if(currentSlide === props.images.length - 1) {
setSelectedImage(props.images[0].img)
setSelectedtitle(props.images[0].title)
setSelectedid(props.images[0]._id)
    } else {
      setSelectedImage(props.images[currentSlide + 1].img);
      setSelectedtitle(props.images[currentSlide + 1].title);
      setSelectedid(props.images[currentSlide + 1]._id);
    }
    
  
  };

  useEffect(() => {
    const interval = setInterval(handleAutoPlay, 5000);

    return () => clearInterval(interval);
  }, [currentSlide]);

  return (
    <div className="slider">
      <div className="current-image">
        {selectedImage && (
          <>
          <img
              src={selectedImage}
              alt="Enlarged Image"
              className="enlarged-image"
            />
          <Link to={`/news/${selectedid}`}>
            <div className="desc">
              {selectedtitle}
            </div>
          </Link>
          </>
        )}
      </div>
      <div className="slides">
        {props.images.map((image, index) => (
          <div
            key={index}
            className={`slide ${index === currentSlide ? "active" : ""}`}
          >
            <img
              src={image.img}
              alt={`Image ${index}`}
              onClick={() => handleImageClick(image.img, image.title)}
            />
          </div>
        ))}
      </div>
    </div>
  );
}

export default Slider;
