import "./sidebar.scss";
import "../LeftHomeOthers/LeftHomeOthers.scss";
import { Link } from "react-router-dom";
import Ad from "../ad/Ad";
import useFetch from "../../hooks/UseFetch";

const Articles = () => {
  const ads = [
    {
      src: "./imgs/ad1.jpg",
      title: "نيو فون لخدمات الجوال",
    },
    {
      src: "./imgs/ad2.jpg",
      title: "الخير للالكترونيات والبطاريات",
    },
    {
      src: "./imgs/ad3.jpg",
      title: "نهر العطور",
    },
  ];
  const { data: artslimit } = useFetch("arts/limit");
  const { data: adsSidebar } = useFetch("ads/dependoncat/?position=in sidebar");

  return (
    <div className="sidebar">
      <div className="LeftHomeOthers">
        <h3>مقالات وكتابات</h3>
        <div className="writes">
          {artslimit.length > 0 &&
            artslimit.map((el) => (
              <Link to={`/arts/${el._id}`}>
                <div className="onewrite">
                  <img src={el.img} alt="" />
                  <div className="info">
                    <div className="title">{el.title}</div>
                    <div className="writer">{el.writer}</div>
                  </div>
                </div>
              </Link>
            ))}
        </div>
        <Link to={`/articles`}>
          <div className="more">
            المزيد <span>+</span>
          </div>
        </Link>
        <div className="ads">
          {adsSidebar.length > 0 &&
            adsSidebar.map((ad) => (
              <>
                <h3>{ad.desc}</h3>
                <Ad adLink={ad.img} adSocial={ad.link} id={ad._id}/>
              </>
            ))}
        </div>
      </div>
    </div>
  );
};

export default Articles;
