import "./homeMainContent.scss";
import LeftHomeOthers from "../LeftHomeOthers/LeftHomeOthers";
import { Link } from "react-router-dom";
import NewsSections from "../newssections/NewsSections";
import useFetch from "../../hooks/UseFetch";

const HomeMainContent = () => {
  const sections = [
    "اخبار وتقارير",
    "محلية",
    "أبين",
    "رياضة",
    "عربية وعالمية",
    "حوارات",
    "اقتصاد",
    "قصص الشهداء",
    "ثقافة وفن",
    "علوم وتقنية",
    "آراء",
    "مجتمع مدني",
  ];
  const { data, loading } = useFetch("news/videos");

  return (
    <div className="homeMainContent">
      <div className="right">
        {sections.map((sec, i) => (
          <div className="section">
            <h2>
              {sec}
              <img
                src="https://www.al-wattan.net/NewThemes/icon-feed.png"
                alt=""
              />
            </h2>
            <div className="bottomContent">
              <NewsSections query={sec} />
            </div>
          </div>
        ))}
      </div>
      <div className="left">
        <h2>
          قــسـم الــفــيديــو
          <img src="./imgs/tv.png" alt="" />
        </h2>
        <div className="videos">
          {data.length > 0 &&
            data.map((el) => (
              <Link to={`/news/${el._id}`}>
                <div className="once">
                  <img src={el.img} alt="" />
                  <div className="desc">{el.title} </div>
                </div>
              </Link>
            ))}
        </div>
        <LeftHomeOthers />
      </div>
    </div>
  );
};

export default HomeMainContent;
