import "./navbar.scss";
import { Link } from "react-router-dom";
import useFetch from "../../hooks/UseFetch";
import moment from "moment";
import "moment/locale/ar";
import Ad from "../ad/Ad";

const Navbar = () => {
  const { data, loading } = useFetch("news");
  const { data: adsInNavbar } = useFetch("ads/dependoncat/?position=in navbar");
  
  return (
    <div className="myNavbar">
      <div className="wrapper">
        <div className="top">
          <div className="date">
            آخر تحديث للموقع :
            <span>
              {data.length > 0 &&
                moment(data[data.length - 1].createdAt)
                  .locale("ar")
                  .format("LLLL")}
            </span>
          </div>
          <ul className="links">
            <Link to={"/page/3"}>
              <li>هيئة التحرير</li>
            </Link>
            <Link to={"/page/2"}>
              <li>من نحن</li>
            </Link>
            <Link to={"/page/1"}>
              <li>إتصـل بـنا</li>
            </Link>
          </ul>
        </div>
      </div>
      <div className="middle">
        <div className="logo">
          <img src="./imgs/logooo.png" alt="" />
          <span>{`{رئيس التحرير/ حاتم عبدالعزيز}`}</span>
        </div>
        <div className="social">
          <img src="./imgs/nabd.png" alt="تطبيق نبض" />
          <img src="./imgs/facebook.png" alt="تطبيق الفيسبوك" />
          <img src="./imgs/google.png" alt="تطبيق جوجل" />
          <img src="./imgs/twitter.png" alt="تطبيق تويتر" />
          <img src="./imgs/youttube.png" alt="تطبيق يوتيوب" />
          <img src="./imgs/telegram.jpg" alt="تطبيق تيليجرام" />
          <img src="./imgs/rss.png" alt="RSS" />
        </div>
      </div>
      <div className="googleAd">
          {adsInNavbar.length > 0 && (
            <>
              <Ad width={"100%"} height={"100%"} id={adsInNavbar[0]._id}
                adLink={adsInNavbar[0].img}
                adSocial={adsInNavbar[0].link}
              />
            </>
          )}
      </div>
      <div className="bwrapper">
        <ul className="bottom">
          <Link to={"/"}>
            <li>
              <img src="./imgs/HB.png" alt="" />
            </li>
          </Link>
          <Link to={"/section/1"}>
            <li>اخبار وتقارير |</li>
          </Link>
          <Link to={"/section/2"}>
            <li>محلية |</li>
          </Link>
          <Link to={"/section/3"}>
            <li>أبين |</li>
          </Link>
          <Link to={"/section/4"}>
            <li> رياضة |</li>
          </Link>
          <Link to={"/section/5"}>
            <li>عربية وعالمية |</li>
          </Link>
          <Link to={"/section/6"}>
            <li>حوارات |</li>
          </Link>
          <Link to={"/section/7"}>
            <li>اقتصاد |</li>
          </Link>
          <Link to={"/section/8"}>
            <li>قصص الشهداء |</li>
          </Link>
          <Link to={"/section/9"}>
            <li>ثقافة وفن |</li>
          </Link>
          <Link to={"/section/10"}>
            <li>علوم وتقنية |</li>
          </Link>
          <Link to={"/section/11"}>
            <li>آراء |</li>
          </Link>
          <Link to={"/section/12"}>
            <li>مجتمع مدني |</li>
          </Link>
          <Link to={"/page/1"}>
            <li>إتصل بنا</li>
          </Link>
        </ul>
      </div>
    </div>
  );
};

export default Navbar;
