import { Link } from "react-router-dom";
import "./footer.scss";

const Footer = () => {
  return (
    <div className="footer">
      <div className="fWrapper">
        <div className="right">
          <div className="top">
            <img src="./imgs/message.png" alt="" />
            <div className="info">
              صحيفة التاسعة برس - جميع الحقوق محفوظة -
              <span>Copyright © 2023</span>
            </div>
          </div>
          <div className="bottom">
            <img src="./imgs/message.png" alt="" />
            <span>برمجة وتطوير</span>
          </div>
        </div>
        <div className="left">
          <Link to={"/page/1"}>
            <span>اتصل بنا |</span>
          </Link>
          <Link to={"/page/3"}>
            <span>هيئة التحرير |</span>
          </Link>

        </div>
      </div>
    </div>
  );
};

export default Footer;
