import { useState } from "react";
import { editorsImages, watchesImages } from "../../data";
import "./taps.scss";
import { Link } from "react-router-dom";
import useFetch from "../../hooks/UseFetch";
const Taps = () => {
  const [currentTap, setCurrentTap] = useState("editors");

  const { data: vdata } = useFetch("news/mostviews");
  const { data: edata } = useFetch("news/editors");

  return (
    <div className="tabs">
      <div className="tapwrapper">
        <div className="top">
          <span
            className={currentTap === "editors" ? "active" : ""}
            onClick={() => setCurrentTap("editors")}
          >
            إختيار المحرر
          </span>
          <span
            className={currentTap === "watches" ? "active" : ""}
            onClick={() => setCurrentTap("watches")}
          >
            الأكثر مشاهدة
          </span>
        </div>
        {currentTap === "editors" && (
          <div className="editors items">
            {edata.length > 0 &&
              edata.map((img, i) => (
                <div className="item" key={i}>
                  <Link to={`/news/${img._id}`}>
                    <img src={img.img} alt="" />
                    <h2>{img.title.substring(0, 50)}...</h2>
                  </Link>
                </div>
              ))}
          </div>
        )}
        {currentTap === "watches" && (
          <div className="watches items">
            {vdata.length > 0 &&
              vdata.map((img, i) => (
                <div className="item" key={i}>
                  <Link to={`/news/${img._id}`}>
                    <img src={img.img} alt="" />
                    <h2>{img.title.substring(0, 50)}...</h2>
                  </Link>
                </div>
              ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default Taps;
