import "./Sections.scss";
import { useLocation } from "react-router-dom";
import { useEffect } from "react";
import { Link } from "react-router-dom";
import { useState } from "react";
import useFetch from "../../hooks/UseFetch";
import moment from 'moment';
import 'moment/locale/ar';
const Sections = () => {
  const location = useLocation();
  const path = location.pathname.split("/")[2];
  const [query, setQuery] = useState("")
  console.log(path);
  useEffect(() => {
    if (path !== "/") {
      const sidebar = document.querySelector(".sidebar");
      const middleContent = document.querySelector(".middleContent");
      sidebar.style.display = "block";
      middleContent.style.display = "flex";
    }
    const q =
      path === "1"
        ? "اخبار وتقارير"
        : path === "2"
        ? "محلية"
        : path === "3"
        ? "أبين"
        : path === "4"
        ? "رياضة"
        : path === "5"
        ? "عربية وعالمية"
        : path === "6"
        ? "حوارات"
        : path === "7"
        ? "اقتصاد"
        : path === "8"
        ? "قصص الشهداء"
        : path === "9"
        ? "ثقافة وفن"
        : path === "10"
        ? "علوم وتقنية"
        : path === "11"
        ? "آراء"
        : path === "12"
        ? "مجتمع مدني"
        : path === "13"
        ? "منوعات"
        : "";
        document.title = q
        setQuery(q)
  }, [path]);
  const {data} = useFetch(`news/dependoncat/?cat=${query}`)
  console.log(data);

  // const [date, setDate] = useState(moment().locale('ar'));

  // useEffect(() => {
  //   const interval = setInterval(() => {
  //     setDate(moment().locale('ar'));
  //   }, 1000);

  //   return () => clearInterval(interval);
  // }, []);
  return (
    <div className="sections">
      <div className="title">
        {path === "1"
          ? "اخبار وتقارير"
          : path === "2"
          ? "محلية"
          : path === "3"
          ? "أبين"
          : path === "4"
          ? "رياضة"
          : path === "5"
          ? "عربية وعالمية"
          : path === "6"
          ? "حوارات"
          : path === "7"
          ? "اقتصاد"
          : path === "8"
          ? "قصص الشهداء"
          : path === "9"
          ? "ثقافة وفن"
          : path === "10"
          ? "علوم وتقنية"
          : path === "11"
          ? "آراء"
          : path === "12"
          ? "مجتمع مدني"
          : path === "13"
          ? "منوعات"
          : ""}
      </div>
     { data.length > 0 && data.map((n) => (
      <div className="oneNews">
        <Link to={`/news/${n._id}`}>
          <div className="oneTitle">
            {n.title}
          </div>
        </Link>
        <div className="time">{moment(n.createdAt).locale('ar').format('LLLL')}</div>
        <div className="oneContent">
          <img
            src={n.img}
            alt=""
          />
          <div className="desc">
            {n.desc.substring(0, 100)}
          </div>
        </div>
      </div>
     )) }
      

    </div>
  );
};

export default Sections;
