import "./otherPages.scss"
import { useLocation } from "react-router-dom";
import { useEffect } from "react";
import ContactUs from "../../components/otherPagesComponents/ContactUs/ContactUs";
import AboutUs from "../../components/otherPagesComponents/AboutUs/AboutUs";
import EditorsUs from "../../components/otherPagesComponents/EditorsUs/EditorsUs";

const OtherPages = () => {
    const location = useLocation();
    const path = location.pathname.split("/")[2];
    console.log(path);
    useEffect(() => {
        if (path !== "/") {
          const sidebar = document.querySelector(".sidebar");
          const middleContent = document.querySelector(".middleContent");
          sidebar.style.display = "block";
          middleContent.style.display = "flex";
        }
      }, [path]);
  return (
    <div className="OtherPages">
        {path === "1" && (<ContactUs />)}
        {path === "2" && (<AboutUs />)}
        {path === "3" && (<EditorsUs />)}
    </div>
  )
}

export default OtherPages